@import '../../styles/mixins';

.popUp {
  position: relative;
  //display: flex;
  //justify-content: center;
  //align-items: center;
  z-index: 20;
  margin-bottom: 70px;

  .popUpContainer {
    display: flex;
    position: relative;
    border-radius: 10px;
    z-index: 24;
    background-color: #222;
    margin: 0 auto;
    height: 600px;
  }

  .btnBlock {
    z-index: 21;
    cursor: pointer;


    .btnPop {
      position: absolute;
      display: flex;
      align-items: center;
      justify-content: center;
      right: -60px;
      top: 5px;
      width: 38px;
      height: 38px;
      text-align: center;
      padding: 3px;
      border: 1px solid floralwhite;
      border-radius: 10px;
      transition: 0.3s;
      color: white;
      font-weight: bold;

      &:hover {
        border-radius: 50%;
      }
    }

    @media screen and (max-width: 950px) {
      .btnPop {
        top: 10px;
        right: 10px;

        width: 24px;
        height: 24px;
        border-radius: 8px;
        font-size: 10px;
      }
    }
  }

  .contentBlock {
    display: flex;
    z-index: 20;
  }


  .contactInfoBlock {
    padding: 50px 40px;

    border-right: 1px dashed hsla(0, 0%, 100%, .15);

    .author {
      min-width: 239px;

      .imgPop {
        margin: 0 auto;
        width: 200px;
        height: 200px;
        margin-bottom: 20px;

        .img {
          display: block;
          object-fit: cover;
          width: 100%;
          height: 200px;
          border-radius: 50%;
        }


      }

      .popName {
        text-align: center;
        text-transform: uppercase;
        color: #f0f0f0;
        font-size: 19px;
        font-weight: 600;

        span {
          color: var(--color-custom);
          font-weight: 600;
        }
      }


      .blockPopJob {
        display: flex;
        justify-content: center;
        align-items: center;

        .popJob {
          color: #a2a2a2;
          margin-bottom: 30px;
          overflow: hidden;
          white-space: nowrap;
          animation: typewriter 3.5s steps(15) infinite alternate,
          blink 800ms steps(15) infinite normal;

          border-right: 3px solid black;

          span {
            display: inline-block;
            color: #f0f0f0;
          }

          @keyframes typewriter {
            0% {
              width: 0;
            }
            20% {
              width: 0;
            }
            70% {
              width: 68%;
            }
            70% {
              width: 68%;
            }
            100% {
              width: 68%;
            }
          }
          @keyframes blink {
            from {
              border-color: var(--color-custom);
            }
            to {
              border-color: transparent;
            }
          }
        }
      }
    }
  }


  @media screen and (max-width: 950px) {
    .contentBlock {
      display: block;
      overflow-y: auto;
    }

    .contactInfoBlock {
      border: none;
    }

    .imgPopBlock {
      margin: 0 auto;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
    }
  }


  .biography {
    color: #a2a2a2;
    padding: 50px 40px;
    width: 500px;
    overflow-y: auto;

    &::-webkit-scrollbar {
      display: none;
      -ms-overflow-style: none;
      scrollbar-width: none;
    }

    .aboutMeBlock {
      margin-bottom: 55px;

      .aboutMeText {
        margin-top: 29px;
      }
    }

    .qualities {
      margin-top: 29px;
      margin-bottom: 55px;

      li {
        position: relative;
        margin: 0 0 8px 15px;
      }

      li::before {
        content: url("../../assets/icon/triangle2.svg");
        position: absolute;
        top: -1px;
        left: -15px;
        color: #32a632;
      }
    }
  }

  @media screen and (max-width: 770px) {
    .biography {
      width: 290px;
    }
  }
}


.mediaBody {
  @include flex(row, center);
  //display: flex;
  border-bottom: 1px dashed hsla(0, 0%, 100%, .15);
  padding: 10px 0;

  .infoIcon {
    padding-right: 37px;
    color: var(--color-custom);

  }

  .btnPop {
    position: relative;
  }
}

.infoName {
  font-size: 16px;
}

@media screen and (max-width: 770px) {
  .popUpContainer {
    width: 300px;
  }
  .contactInfoBlock .author .imgPop {
    .img {
      height: 130px;
      //height: 130px;
    }
  }

  .infoName {
    font-size: 14px;
  }
  .mediaBody {
    width: 220px;

    .infoIcon {
      padding-right: 24px;
    }
  }
}


